.app {
  --scalePercent: 0;
  background-image: url('./images/profile.webp');
  background-size: auto calc(135% + var(--scalePercent));
  background-color: #222222;
  width: 100%;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  position: relative;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  -webkit-transition: background-size 200ms;
  -o-transition: background-size 200ms;
  transition: background-size 200ms;
}

.triangle-left {
  width: 0;
  height: 0;
  border-top: 50vw solid rgba(0,0,0,.7);
  border-right: 100vh solid transparent;
  position: absolute;
}

.triangle-right {
  width: 0;
  height: 0;
  border-bottom: 50vw solid rgba(0,0,0,.7);
  border-left: 100vh solid transparent;
  position: absolute;
  right: 0;
  bottom: 0;
}

.author {
  position: absolute;
  top: 50%;
  left: 5px;
  -webkit-transform: translate(-45%, -50%) rotate(-90deg);
      -ms-transform: translate(-45%, -50%) rotate(-90deg);
          transform: translate(-45%, -50%) rotate(-90deg);
  color: #fff;
}


